import { ErrorHandler } from './ErrorHandler';
import { getResolvedErrorSrc176Evid101 } from '@wix/error-handler-core/@wix/bi-logger-os-data/v2';
import { withoutDefaults } from '../util/withoutDefaults';
import { ErrorAccessor } from '../state/ErrorAccessor';
import { ErrorResolution } from '../state/ErrorResolution';
import { ShowErrorActionProps } from '@wix/error-handler-core/types';

export function reportGetResolvedErrorBi(
  errorHandler: ErrorHandler,
  params: {
    readonly message: string;
    readonly action?: ShowErrorActionProps;
    readonly errorAccessor: ErrorAccessor;
    readonly errorResolution: ErrorResolution | null | undefined;
  },
) {
  const { message, action, errorAccessor, errorResolution } = params;
  const { biLogger, commonErrorsMapState, translations } = errorHandler;
  const { errorHandlerState } = errorAccessor;

  const { consumerResolvedError } = errorResolution ?? {};

  biLogger.report(
    withoutDefaults(getResolvedErrorSrc176Evid101)({
      session_id: errorHandlerState?.sessionId,
      translated_message: message,
      action_text: action?.text,
      is_consumer_resolved_error: consumerResolvedError != null,
      common_errors_load_failed: String(
        commonErrorsMapState.commonErrors == null,
      ),
      isEn: translations.i18n.language === 'en',
    }),
  );
}
